export default {
	data() {
		return {
			dataList:[],
			pageIndex:1,
			pageSize:10,
			pageTotal:1,
			nomore:false,
			nodata:false
		}
	},
	onLoad(){
		uni.setNavigationBarColor({
			backgroundColor: "#60ACF8",
			frontColor: '#ffffff',
			animation: {
				duration: 400,
				timingFunc: 'easeIn'
			}
		})
	},
	onShow(){
		this.initData()
	},
	onPullDownRefresh(){
		this.resetData()
		this.initData()
		uni.stopPullDownRefresh();
	},
	onReachBottom(){
		if (this.pageIndex < this.pageTotal) {
			this.pageIndex++
			this.initData()
		} else {
			this.nomore = true
			return false;
		}
	},
	methods: {
		initData(){
			let self = this
			let params = {
				pageSize:this.pageSize,
				pageIndex:this.pageIndex
			}
			this.$acFrame.HttpService.inviteRank(params).then(res=>{
				if(res.success){
					self.pageTotal=res.data.pageTotal
					let list = res.data.rows
					self.pageSize = res.data.pageSize
					if(list.length>0){
						self.dataList =self.dataList.concat(res.data.rows);
					}else{
						self.nodata=true
					}
					
				}
			})
		},
		setImg(src){
			return  this.$acFrame.Util.setImgUrl(src);
		},
		resetData(){
			this.pageSize = 10
			this.pageIndex = 1
			this.nodata = false
			this.nomore = false
			this.dataList=[]
		}
	}
}
